.job-entry {
  position: relative;
}

.job-entry>h3 {
  position: absolute;
  left: -24px;
  top: -11px;
}

.job-entry .workpanel-marker {
  margin-bottom: 20px;
}

.workpanel-marker {
  background-color: black;
  display: block;
  border-radius: .2rem;
  width: 40px;
  height: 40px;
  margin-left: 55px;
}

.workpanel-marker-wish {
  background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(50%, white), color-stop(50%, #2fb7eb), to(#2fb7eb) );
  background: -webkit-linear-gradient( bottom, white 0%, white 50%, #2fb7eb 50%, #2fb7eb 100% );
  background: -o-linear-gradient( bottom, white 0%, white 50%, #2fb7eb 50%, #2fb7eb 100% );
  background: linear-gradient( 0deg, white 0%, white 50%, #2fb7eb 50%, #2fb7eb 100% );
}

.workpanel-marker-amazon {
  background: -webkit-gradient( linear, left bottom, left top, from(#e67e22), color-stop(50%, #e67e22), color-stop(50%, #eee), to(#eee));
  background: -webkit-linear-gradient( bottom, #e67e22 0%, #e67e22 50%, #eee 50%, #eee 100%);
  background: -o-linear-gradient( bottom, #e67e22 0%, #e67e22 50%, #eee 50%, #eee 100%);
  background: linear-gradient( 0deg, #e67e22 0%, #e67e22 50%, #eee 50%, #eee 100%);
}

.workpanel-marker-southpaw {
  background: -webkit-gradient( linear, left bottom, left top, from(#2e3234), color-stop(50%, #2e3234), color-stop(50%, #e67e22), to(#e67e22));
  background: -webkit-linear-gradient( bottom, #2e3234 0%, #2e3234 50%, #e67e22 50%, #e67e22 100%);
  background: -o-linear-gradient( bottom, #2e3234 0%, #2e3234 50%, #e67e22 50%, #e67e22 100%);
  background: linear-gradient( 0deg, #2e3234 0%, #2e3234 50%, #e67e22 50%, #e67e22 100%);
}

.workpanel-marker-uoft {
  background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(50%, white), color-stop(50%, #002a5c), to(#002a5c));
  background: -webkit-linear-gradient( bottom, white 0%, white 50%, #002a5c 50%, #002a5c 100%);
  background: -o-linear-gradient( bottom, white 0%, white 50%, #002a5c 50%, #002a5c 100%);
  background: linear-gradient( 0deg, white 0%, white 50%, #002a5c 50%, #002a5c 100%);
}

.workpanels .timeline-entry {
  background-color: #f4f4f4;
  display: block;
  border-radius: .2rem;
  left: 107px;
  padding: 10px;
  position: relative;
  top: -60px;
  width: calc(100% - 150px);
}

.timeline-entry-title>h4 {
  font-family: 'Ubuntu Mono', monospace;
  display: inline;
}


